.details-restaurant {
  max-width: 1280px;
  margin: 0 auto;
  height: 735px;
  

  background-image: linear-gradient(
      to right,
      rgba(0, 0, 0, 0.65),
      rgba(0, 0, 0, 0.65)
      ),
    url(../images/homepage/mob/hp-rest-bkg-mob_1x.jpg);
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center center;

  @media (min-device-pixel-ratio: 2),
    (-webkit-min-device-pixel-ratio: 2),
    (min-resolution: 192dpi),
    (min-resolution: 2dppx) {
    & {
      background-image: linear-gradient(
          to right,
          rgba(0, 0, 0, 0.65),
          rgba(0, 0, 0, 0.65)
        ),
        url(../images/homepage/mob/hp-rest-bkg-mob_2x.jpg);
    }
  }

  padding-bottom: 40px;
  padding-top: 24px;

  @include tablet {
    background-image: linear-gradient(
        to right,
        rgba(0, 0, 0, 0.65),
        rgba(0, 0, 0, 0.65)
      ),
      url(../images/homepage/tablet/hp-rest-bkg-tab_1x.jpg);
    background-size: cover;
    background-repeat: no-repeat;
    background-position: center center;

    @media (min-device-pixel-ratio: 2),
      (-webkit-min-device-pixel-ratio: 2),
      (min-resolution: 192dpi),
      (min-resolution: 2dppx) {
      & {
        background-image: linear-gradient(
            to right,
            rgba(0, 0, 0, 0.65),
            rgba(0, 0, 0, 0.65)
          ),
          url(../images/homepage/tablet/hp-rest-bkg-tab_2x.jpg);
      }
    }

    // ТИМЧАСОВО
    height: 620px;
    // ТИМЧАСОВО

    padding-bottom: 48px;
    padding-top: 48px;
  }

  @include pc {
    background-image: linear-gradient(
        to right,
        rgba(0, 0, 0, 0.65),
        rgba(0, 0, 0, 0.65)
      ),
      url(../images/homepage/pc/hp-rest-bkg-pc_1x.jpg);
    background-size: cover;
    background-repeat: no-repeat;
    background-position: center center;

    @media (min-device-pixel-ratio: 2),
      (-webkit-min-device-pixel-ratio: 2),
      (min-resolution: 192dpi),
      (min-resolution: 2dppx) {
      & {
        background-image: linear-gradient(
            to right,
            rgba(0, 0, 0, 0.65),
            rgba(0, 0, 0, 0.65)
          ),
          url(../images/homepage/pc/hp-rest-bkg-pc_2x.jpg);
      }

      padding-bottom: 48px;
      padding-top: 48px;
    }
  }

  &__logo {
    color: $color-primary;
    font-size: 40px;
    margin-bottom: 12px;
    line-height: 1.075;
    letter-spacing: 0.3em;

    @include tablet {
      margin-right: -0.3em;
      font-size: 64px;
      line-height: 1.078;
      margin-bottom: 16px;
    }
  }

  &__title {
    font-family: 'Montserrat';
    font-weight: 700;
    font-size: 20px;
    line-height: 1.2;
    color: $color-primary;
    margin-bottom: 20px;
    letter-spacing: 0.02em;

    @include tablet {
      font-size: 24px;
      line-height: 1.2;
      margin-bottom: 8px;
    }
  }

  &__caption {
    font-weight: 600;
    font-size: 12px;
    line-height: 1.25;
    letter-spacing: 0.02em;
    color: $color-text-tertiary;
    margin-bottom: 48px;

    @include tablet {
      margin-bottom: 40px;
      font-size: 16px;
    }
  }

  &__container {
    display: block;

    width: 259px;
    margin-right: auto;
    margin-left: auto;

    @include tablet {
      // margin-right: 0px;
      width: 549px;
      display: flex;
      flex-direction: column;
    }

    @include pc {
      width: 595px;
    }
  }

  &__titles {
    display: flex;
    flex-direction: column;
    margin-left: auto;
    margin-right: auto;

    @include tablet {
      margin: 0;
    }
  }

  &__button {
    text-decoration: none;

    @extend %cubic;

    &:hover,
    &:focus {
      background-color: $color-primary;
      color: $color-tertiary;
    }
  }

  &__buttons {
    gap: 16px;
    display: flex;
    flex-direction: column;
    align-items: center;

    @include tablet {
      display: flex;
      flex-direction: column;
      justify-content: flex-end;

      width: 236px;
      position: relative;
      bottom: 152px;
      right: 324px;
    }

    @include pc {
      bottom: 154px;
      right: 436px;
    }
  }

  &__wrapper {
    overflow-x: hidden;
    @media (max-width: 414px) {
      overflow: hidden;
    }

    position: relative;
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: space-between;

    @include tablet {
      display: flex;
      align-items: center;
      padding: 0 48px;

      min-height: 620px;
    }
  }

  &__image {
    width: 270px;
    height: 270px;
  }

  &__swiperf {
    width: 414px;

    gap: 20px;
    transform: translatex(-108%);
    display: flex;

    margin-bottom: 16px;

    @include tablet {
      width: 1024px;
    }
  }

  &__arrows {
    border: none;
    background-color: transparent;
    fill: white;
    padding: 0;

    &:first-child {
      padding-right: 30px;
    }
  }

  &__controls {
    margin-bottom: 32px;

    width: 414px;
    display: flex;
    justify-content: flex-end;
  }
}

.details-restaurant-slider-container {
  display: flex;
  justify-content: flex-end;
  position: absolute;
  right: -480px;
  bottom: 200px;

  @include tablet {
    bottom: 151px;
    right: -220px;
  }

  @include pc {
    bottom: 156px;
    right: -35px;
  }
}

.details-restaurant-slick__list .my-slick-prev,
.details-restaurant-slick__list .my-slick-next {
  position: absolute;
  bottom: -52px;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 0;
  width: 32px;
  height: 32px;
  cursor: pointer;
  border: none;
  background-color: transparent;

  @include tablet {
    bottom: -64px;
  }

  @include pc {
    bottom: -68px;
  }
}

